body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body.simple-login-app {
  background-color: transparent;
}
.route-holder{
  min-height: calc(100vh - 100px);
}
.site-footer{
  display: flex;
  align-items: center;
  justify-content: center;
  background: #606c88;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #3f4c6b, #606c88); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  color: #fff;
}
.app-background{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  background: #7F7FD5;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #91EAE4, #86A8E7, #7F7FD5); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.under-construction,
.not-found{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: calc(100vh - 150px);
  color: #fff;
  font-size: 4rem;
}

.site-header,
.site-footer{
  height: 50px;
}
.site-header .nav{
  height: 100%;
}
.site-header a {
	color: inherit;
}
.site-header .nav-tabs{
  border : none;
  background: #606c88;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #3f4c6b, #606c88); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  color: #fff;
}
.site-header .nav-tabs .nav-item{
	margin-bottom: 0
}
.site-header .nav .nav-link{
  display: flex;
  align-items: center;
  height: 100%;
  border: none;
  border-radius : 0.25rem;
}
.site-header .nav-tabs .nav-link:hover,
.site-header .nav-tabs .nav-link.active,
.site-header .nav-tabs .nav-item.show .nav-link{
	background: #0F2027;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(to right, #2C5364, #203A43, #0F2027); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
	color: #fff;
}
.shimmer-container{
	width: 100%;
}
.shimmer-loader{
	background-color: #f2f2f2;
	border-radius: 8px;
}
.shimmer-container .shimmer-image{
	height: 480px;
	margin-bottom: 10px;
}
.shimmer-container .shimmer-text{
	height: 15px;
	margin-top: 10px;
}
.shimmer-container .first-text{
	width: 75%;
}
.shimmer-container .second-text{
	width: 90%;
}
.shimmer-container .third-text{
	width: 50%;
}
.route-holder.social{
	background: #eaebef;
}
.white-card{
	background-color: #fff;
	border-radius: 6px;
	overflow: hidden;
	box-shadow: 0 0 6px 0 rgba(0,0,0,0.32);
	padding: 15px;
	margin-top: 20px;
	position: relative;
}
.white-card:first-child{
	margin-top: 0;
}
.social-posts-wrapper{
	width: 50%;
	padding: 20px 0;
	margin: auto;
}
/*common slider*/
.slider-wrapper {
  position: relative; }
  .slider-wrapper .slider-count {
    width: 50px;
    position: absolute;
    top: 10px;
    left: 10px;
    color: #fff;
    z-index: 2; }

/*common slider end*/
body.social .gymain {
  width: 100%;
  max-width: 1366px; }

body.social .mid_outer {
  width: calc(100% - 30px);
  margin: auto;
  float: none;
  box-shadow: none;
  background: transparent; }

body.social .rt_outer {
  width: 100%;
  float: none;
  padding: 0; }

.post-list-container {
  margin: auto; }
  .post-list-container .post-holder {
    position: relative;
    margin-bottom: 20px; }

/*bottom bar*/
.bottom-bar {
  width: 100%;
  height: 45px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: #333;
  color: #fff;
  padding: 0 10px;
  cursor: pointer; }

.bottom-bar:hover {
  color: #fff; }

/*bottom bar end*/
/*more options*/
.post-list-container .more-options {
  position: absolute;
  right: 4px;
  top: 4px;
  z-index: 5; }
  .post-list-container .more-options .icon-wrapper {
    height: 32px;
    width: 32px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #333;
    cursor: pointer; }
    .post-list-container .more-options .icon-wrapper span {
      color: inherit; }
  .post-list-container .more-options .more-tooltip {
    position: absolute;
    min-width: 180px;
    right: 18px;
    top: 18px;
    background: #FFFFFF;
    box-shadow: 0 0 6px 2px rgba(0, 0, 0, 0.3);
    border-radius: 5px;
    overflow: hidden;
    font-size: 14px; }
    .post-list-container .more-options .more-tooltip .more-item {
      min-height: 40px;
      display: flex;
      align-items: center;
      padding: 0 10px;
      cursor: pointer; }
      .post-list-container .more-options .more-tooltip .more-item .item-icon {
        margin-right: 5px;
        color: #666;
        font-size: 16px; }
    .post-list-container .more-options .more-tooltip .more-item:first-child {
      margin-top: 10px; }
    .post-list-container .more-options .more-tooltip .more-item:last-child {
      margin-bottom: 10px; }
    .post-list-container .more-options .more-tooltip .more-item:hover {
      background: rgba(74, 144, 226, 0.1); }

.post-holder.hidden-post .more-options .icon-wrapper {
  color: #fff; }

/*more options end*/
/*hidden msg*/
.hidden-msg {
  position: absolute;
  top: 0;
  z-index: 3;
  width: 100%;
  min-height: 55px;
  background: rgba(0, 0, 0, 0.7);
  color: #fff;
  padding: 10px 20px;
  display: flex;
  align-items: center; }
  .hidden-msg .icon-hide {
    align-self: flex-start;
    margin-top: 2px;
    margin-right: 5px; }

/*product post*/
.products-post-container .product-details {
  padding: 10px;
  color: #333;
  background: #EDF7FF; }
  .products-post-container .product-details .icon-currency {
    font-size: 15px;
    color: inherit;
    position: relative;
    top: 2px;
    margin-right: 2px; }

.products-post-container .product-price {
  color: #018100; }

/*product post end*/

